var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('Content',[_c('Note',[_vm._v("Menus is the only non-browser component. Check and run the sample repos to see it in action.")]),_c('Markdown-Snippet',{attrs:{"content":"Invisible component which reactively handles context and flyout menus. Supports multiple instances and will rebuild as the most recently mounted instance (in the event you need different menus per router page or other use case). Any changes to the `:context` or `:flyout` Arrays will trigger a full rebuild of the menu in real-time."}}),_c('h2',[_vm._v("Props")]),_c('Coder',{attrs:{"content":_vm.getChildByName('Props').data}}),_c('div',{staticClass:"spacer"}),_c('Table',{attrs:{"content":_vm.tableData.properties}}),_c('Markdown-Snippet',{attrs:{"content":"> ***NOTE:*** To include callbacks into `:context` or `:flyout` within the same file, you must use `this.callback` with `data: { return {} }` syntax, or `callback` with `data: () => ({})` syntax. Do not initialize functions ( e.g. use `this.callback` and not `this.callback()` )."}}),_c('h2',[_vm._v("Menu Item Props")]),_c('Table',{attrs:{"content":_vm.tableData.menus}}),_c('h2',[_vm._v("Examples")]),_c('Markdown-Snippet',{attrs:{"content":"Menus contain only `Copy debug URL` and `Refresh Panel`:\n\n```html\n  <Menus refresh debug />\n```\n\nMenus contain reload and a custom context menu, passing an event to a local method each time the context menu is clicked. Note that `this.doSomething` is called for both menu items, though the last item will trigger `this.localMethod` after the `@contextClick` event.\n```html\n<Menus refresh\n  @contextClick=\"this.doSomething\"\n  :context=\"[\n      {\n        label: `This is a disabled menu item`,\n        enabled: false\n      },\n      {\n        label: `Toggle value of this.data.something`,\n        checkable: true,\n        checked: something,\n        callback: (item, index, value) => (something = value)\n      },\n      {\n        label: `This activates a method within this file on clicking this item`,\n        enabled: true,\n        callback: this.localMethod\n      }\n  ]\"\n/>\n```\n\nContext menu contains a menu item with icon, then a divider, then a nested menu group:\n\n```html\n<Menus\n  :context=\"[\n    {\n      label: `Menu item has a custom icon`,\n      icon: `./src/assets/MyMenuIcon.png`\n    },\n    { label: `---` },\n    {\n      label: `Nested item`,\n      menu: [\n        { label: `Hello` },\n        { label: `Goodbye` }\n      ]\n    }\n  ]\"\n/>\n```\n\nRedress flyout menu contents based on a property:\n\n```html\n<Menu \n  :flyout=\"showExtendedMenu ? extendedMenu : baseMenu\"\n/>\n```\n\nThe above could be done much better with a computed property like so, which will automatically rebuild itself any time the property value is changed:\n\n```html\n<template>\n  <Menu :flyout=\"flyoutMenu\" />\n<template>\n```\n\n```html\n<script>\nexport default {\n  data: () => ({\n    showExtendedMenu: true,\n    extendedMenu: [\n      { label: `Hello` },\n      { label: `World` }\n    ],\n    baseMenu: [\n      { label: `Hello World` }\n    ]\n  }),\n  computed: {\n    flyoutMenu() {\n      return this.showExtendedMenu ? this.extendedMenu : this.baseMenu,\n    },\n  }\n}\n</script>\n```"}})],1)}
